.Bet {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 150px;
  margin-left: 20px;
  text-align: center;
  color: white;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #fff, 0 0 40px #0ff, 0 0 80px #0ff, 0 0 90px #0ff, 0 0 100px #0ff, 0 0 150px #0ff;
  background: black;
  border: 1px solid #4c9f8f;
  border-radius: 10px;
  box-shadow: 0 0 10px #bbfef1, 0 0 10px #bbfef1 inset;
  transition: all .2s linear; }
  .Bet .bet-control {
    display: flex;
    flex-direction: column; }
  .Bet .betUp, .Bet .betDown {
    outline: none;
    cursor: pointer;
    color: white;
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #fff, 0 0 40px #0ff, 0 0 80px #0ff, 0 0 90px #0ff, 0 0 100px #0ff, 0 0 150px #0ff;
    background: #4c9f8f;
    border: 1px solid #4c9f8f;
    border-radius: 30px;
    box-shadow: 0 0 10px #bbfef1, 0 0 10px #bbfef1 inset;
    transition: all .2s linear; }
    .Bet .betUp:hover, .Bet .betDown:hover {
      box-shadow: 0 0 20px #bbfef1, 0 0 25px #bbfef1 inset; }
