.Slot {
  position: absolute;
  top: -2000px;
  left: 0;
  display: flex;
  flex-direction: column;
  width: 30%;
  padding: 10px;
  filter: blur(0px);
  transition: 1s ease-in-out;

  &:nth-child(2) {
    left: 32.4%;
  }

  &:nth-child(3) {
    left: 65%;
  }

  .Slot__item {
    display: flex;
    justify-content: center;
    width: 180px;
    min-height: 120px;
    color: transparent;
    font-size: 2.5rem;
    background-size: contain;
    background-repeat: no-repeat;

    --primary-neon:rgb(80, 234, 204);
    --light-neon:rgb(187, 254, 241);
    --pulse-neon:rgb(76, 159, 143);
    --bright-neon:rgb(144, 185, 177);
    --off-neon: #333;
    
    --neon-shadow:0 0 7px 4px var(--light-neon), 0 0 7px 4px var(--light-neon) inset;
    --neon-shadow-pulse:0 0 8px 5px var(--pulse-neon), 0 0 8px 5px var(--pulse-neon) inset;
    --neon-shadow-bright:0 0 8px 4px var(--bright-neon), 0 0 8px 4px var(--bright-neon) inset;
    --neon-shadow-text:0 0 7px var(--primary-neon);
    
    border:3px solid var(--primary-neon);
    border-radius:5px;
    display:flex;
    align-items:center;
    justify-content:center;
    text-transform:uppercase;
    color: transparent;
    font-size:3vw;
    font-weight:bold;
    box-shadow:var(--neon-shadow);
    animation:flicker 8s linear infinite, moveSymbol 1s;
  }
}

@keyframes moveSymbol {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes flicker {
  0% {
    box-shadow:var(--neon-shadow);
  }
  45% {
    box-shadow:var(--neon-shadow-pulse);
  }
  90% {
    box-shadow:var(--neon-shadow);
    border-color:var(--primary-neon);
  }
  90.01% {
    box-shadow:none;
    border-color:var(--off-neon);
  }
  94% {
    box-shadow:none;
    border-color:var(--off-neon);
  }
  94.01% {
    box-shadow:var(--neon-shadow);
    border-color:var(--primary-neon);
  }
  95% {
    box-shadow:var(--neon-shadow);
    border-color:var(--primary-neon);
  }
  95.01% {
    box-shadow:none;
    border-color:var(--off-neon);
  }
  95.5% {
    box-shadow:none;
    border-color:var(--off-neon);
  }
  95.51% {
    box-shadow:var(--neon-shadow);
    border-color:var(--primary-neon);
  }
  96% {
    box-shadow:var(--neon-shadow);
    border-color:var(--primary-neon);
  }
  96.01% {
    box-shadow:none;
    border-color:var(--off-neon);
  }
  96.5% {
    box-shadow:none;
    border-color:var(--off-neon);
  }
  96.51% {
    box-shadow:var(--neon-shadow-bright);
    border-color:var(--primary-neon);
  }
  100% {
    box-shadow:var(--neon-shadow);
    border-color:var(--primary-neon);
  }
}